<template>
<div id="shopping-list">
  <nav class="top-nav navbar is-fixed-top">
    <p>åè¨(ç¨è¾¼): {{ totalTaxIn }} å</p>
  </nav>
  <div class="main-container container">
    <item-list
      v-for="(item, idx) in items"
      :key="idx"
      v-bind:items="items"
      v-bind:idx="idx"
      @remove="items.splice(idx, 1)"/>
    <button @click="addRow" class="button is-primary is-outlined is-small">add</button>
  </div>
</div>
</template>

<script>
import ItemList from '@/components/ItemList'
export default {
  name: 'list-container',
  data () {
    return {
      items: [''],
      totalTaxIn: 0
    }
  },
  updated () {
    let result = this.items.map(Number).reduce(function (sum, price) {
      return sum + price
    })
    const formatter = new Intl.NumberFormat('ja-JP')
    this.totalTaxIn = formatter.format(Math.floor(result * 1.08))
  },
  methods: {
    addRow () {
      this.items.push('')
    }
  },
  components: {
    ItemList
  }
}
</script>

<style lang="scss" scoped>
@import "../../node_modules/bulma/bulma.sass";
.top-nav{
  line-height: 3.25rem;
  vertical-align: middle;
}
.main-container {
  margin-top: 60px;
}
</style>



