<template>
  <div id="app">
    <h2>Monthly Billing of CreditCard</h2>
    <p>åè¨: ï¿¥{{this.summaryAmount.toLocaleString()}}</p>
    <div>
      <select name="date" id="date" v-model="targetDate">
        <option v-for="option in dateSelectOptions" :key="option.value" :value="option.value">
            {{ option.text }}
        </option>
      </select>
      <button @click="fetchZaim">SHOW</button>
      <div v-show="histories.length">
        <input type="radio" name="is-fixed-cost" id="fc-fca" v-model="isfixedcost" value="all"><label for="fc-fca">å¨ã¦</label>
        <input type="radio" name="is-fixed-cost" id="fc-fcy" v-model="isfixedcost" value="åºå®è²»" ><label for="fc-fcy">åºå®è²»</label>
        <input type="radio" name="is-fixed-cost" id="fc-fcn" v-model="isfixedcost" value="" ><label for="fc-fcn">åºå®è²»ä»¥å¤</label>
      </div>
    </div>
    <ul>
      <li class="money-list" v-for="money in histories" :key="money.id">
        <div class="list-inner-container">
          <div class="list-inner-container-left">
            <span class="list-amount">ï¿¥{{ money.amount.toLocaleString() }}</span>
          </div>
          <div class="list-inner-container-right">
            <span class="list-date">{{ money.date }}</span>
          </div>
        </div>
        <span class="list-genre">{{ money.genre_id }}</span><span class="p-comment">{{ money.place }}</span>
        <div class="list-bottom">
          <p class="p-comment">{{ money.comment }}</p>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  data () {
    return {
      moneyHistories: [],
      tmpData: [],
      targetDate: this.getTargetDate(),
      dateSelectOptions: this.selectOptions(),
      isfixedcost: 'all'
    }
  },
  methods: {
    fetchZaim: function () {
      // let uri = 'http://localhost:4567/zaim/creditcard?mapping=1&start_date=' + this.getStartDate + '&end_date=' + this.getEndDate
      let uri = 'https://myzaim.herokuapp.com/zaim/creditcard?mapping=1&start_date=' + this.getStartDate + '&end_date=' + this.getEndDate
      axios.get(uri)
        .then(res => {
          this.moneyHistories = res.data
        })
    },
    zeroPadding: function (num, length) {
      return ('0000000000' + num).slice(-length)
    },
    selectOptions: function () {
      let date = new Date()
      // 1æ¥ãæç¤ºçã«ã»ããããªãã¨æãæ¸ããã¨ãã«ä¸å·åãåºã
      date.setDate(1)
      // ç¿æããé¸æå¯è½
      date.setMonth(date.getMonth() + 1)
      let dateList = []
      do {
        let year = date.getFullYear()
        let month = this.zeroPadding(date.getMonth() + 1, 2)
        dateList.push({text: year + '-' + month, value: year + '' + month})
        // 2018å¹´8æã¾ã§é¸æå¯è½
        if (year + month === '201808') { break }

        // FIXME:3æã2ã¤åºããã°ãã
        date.setMonth(date.getMonth() - 1)
      } while (true)
      return dateList
    },
    getTargetDate: function () {
      let date = new Date()
      let year = date.getFullYear()
      let month = date.getMonth() + 1
      return year + '' + this.zeroPadding(month, 2)
    }
  },
  computed: {
    getStartDate: function () {
      let year = this.targetDate.slice(0, 4)
      let month = this.targetDate.slice(4)
      let date = new Date(year, month - 1, 1)
      let targetMonth = date.getMonth() + 1
      return date.getFullYear() + '-' + targetMonth + '-' + date.getDate()
    },
    getEndDate: function () {
      let year = this.targetDate.slice(0, 4)
      let month = this.targetDate.slice(4)
      let date = new Date(year, month, 0)
      let targetMonth = date.getMonth() + 1
      return date.getFullYear() + '-' + targetMonth + '-' + date.getDate()
    },
    summaryAmount: function () {
      return this.moneyHistories.reduce((sum, money, index, array) => {
        if (this.isfixedcost === 'all' || money.place === this.isfixedcost) {
          return sum + money.amount
        }
        return sum
      }, 0)
    },
    histories: function () {
      // å¨ã¦ãªãä½ãããªã
      // åºå®è²»ãªãplace === 'åºå®è²»'ã®ã¿
      // åºå®è²»ä»¥å¤ãªãplace !== 'åºå®è²»'
      if (this.moneyHistories === undefined) { return }
      if (this.isfixedcost === 'all') {
        return this.moneyHistories
      }
      return this.moneyHistories.filter((element) => {
        return element.place === this.isfixedcost
      })
    }
  }
}
</script>

<style scoped>
  ul {
    padding: 0px;
  }
  .money-list {
    list-style-type: none;
    background-color: #B8E9FE;
    background: linear-gradient(#83d4fb, #B8EAC0);
    border-radius: 10px;
    margin: 10px 15px;
    padding: 2px 5px 5px;
  }
  .list-inner-container {
    display: flex;
    justify-content: space-between;
    font-family: 'Noto Sans JP', sans-serif
  }
  .list-inner-container-right {
    margin: 5px;
  }
  .list-bottom {
    background-color: aliceblue;
    border-radius: 3px;
  }
  .p-comment {
    margin: 0px;
    padding: 5px;
    font-size: 12px;
  }
  .list-amount {
    font-size: 20px;
  }
  .list-date {
    font-size: 13px;
  }
  .list-genre {
    margin: 0px;
    font-size: 13px;
  }
  /* <link href="https://fonts.googleapis.com/css?family=Noto+Sans+JP" rel="stylesheet"> */
</style>



